@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserratregular.woff2") format("woff2"), url("../fonts/montserratregular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/montserratmedium.woff2") format("woff2"), url("../fonts/montserratmedium.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/montserratsemibold.woff2") format("woff2"), url("../fonts/montserratsemibold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/montserratbold.woff2") format("woff2"), url("../fonts/montserrabold.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.wrapper.menu-open::before {
  position: absolute;
  content: "";
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 43, 102, 0.5);
  z-index: 1;
}

.wrapper.menu-open .main-header__logo {
  left: 51px;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding: 0 10px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.btn {
  display: inline-flex;
  vertical-align: middle;
  padding: 10px 20px;
  user-select: none;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.main-header {
  background-color: #011c40;
}

.main-header__container {
  display: flex;
  align-items: center;
  width: 1180px;
  max-width: 100%;
  padding-top: 35px;
}
@media (max-width: 1023px) {
  .main-header__container {
    width: 100%;
    padding-top: 30px;
    padding-right: 47px;
  }
}
@media (max-width: 767px) {
  .main-header__container {
    display: block;
    padding: 0;
  }
}

.main-header__logo {
  align-self: start;
  margin-right: auto;
  z-index: 1;
}
@media (max-width: 767px) {
  .main-header__logo {
    position: absolute;
    top: 21px;
    left: 17px;
    z-index: 2;
  }
}

.main-header__navigation {
  z-index: 1;
}
@media (max-width: 1023px) {
  .main-header__navigation {
    margin-top: -2px;
  }
}

@media (max-width: 767px) {
  .main-header__navigation .main-navigation__list {
    padding-top: 75px;
    background-color: #f9fbfd;
  }
}

@media (max-width: 767px) {
  .main-header__navigation .main-navigation__link {
    padding-left: 25px;
    color: #011c40;
  }
}

@media (max-width: 767px) {
  .main-navigation--closed .main-navigation__item {
    display: none;
  }
  .main-navigation--opened .main-navigation__wrapper {
    position: absolute;
    top: 0;
    left: 35px;
    right: 0;
    z-index: 1;
  }
  .main-navigation--opened .main-navigation__toggle {
    right: 7px;
    background-image: url("../img/svg/mobile-menu-icon-close.svg");
    background-position: center;
    background-repeat: no-repeat;
  }
  .main-navigation--closed .main-navigation__toggle {
    background-image: url("../img/svg/mobile-menu-icon-open.svg");
    background-position: center;
    background-repeat: no-repeat;
  }
  .main-navigation--opened .main-navigation__list {
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: start;
    overflow-x: auto;
  }
  .main-navigation--nojs .main-navigation__toggle {
    display: none;
  }
  .main-navigation--nojs .main-navigation__wrapper {
    position: static;
    min-height: 0;
  }
  .main-navigation--nojs .main-navigation__item {
    display: block;
  }
}
.main-navigation__list {
  display: grid;
  grid-template-columns: repeat(4, auto);
  gap: 33px;
  margin: 0;
  padding-left: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  list-style-type: none;
}
@media (max-width: 1023px) {
  .main-navigation__list {
    gap: 23px;
  }
}
@media (max-width: 767px) {
  .main-navigation__list {
    grid-template-columns: 1fr;
    gap: 8px;
  }
}

.main-navigation__link {
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: #f9fbfd;
  user-select: none;
  transition: opacity 0.3s ease;
}
@media (max-width: 1023px) {
  .main-navigation__link {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .main-navigation__link {
    display: block;
    padding: 10px;
    font-size: 18px;
    line-height: 22px;
  }
}
@media (hover: hover) {
  .main-navigation__link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.main-navigation__link.focus-visible:focus {
  opacity: 0.8;
}

.main-navigation__toggle {
  display: none;
  background-color: transparent;
}
@media (max-width: 767px) {
  .main-navigation__toggle {
    position: absolute;
    top: 16px;
    right: 13px;
    display: block;
    width: 30px;
    height: 30px;
    margin: 0;
    padding: 0;
    border: none;
    cursor: pointer;
    z-index: 2;
  }
}

